.digital-marketing-comp-main-component {
    width: 100%;
    height: 95%;
    // background-color: orangered;
    display: flex;
    justify-content: center;
    position: relative;

    .shape1-div {
        position: absolute;
        width: 5%;
        height: 22%;
        background-color: #ff4d4d;
        bottom: 0;
        left: 0;
        border-top-right-radius: 60px;
    }

    .slider-div {
        width: 85%;
        height: 82%;
        // border: 1px solid black;
        display: flex;
        justify-content: center;
        align-items: center;
        position: relative;
        overflow: hidden;

        .upper-div {
            position: absolute;
            z-index: 1;
            width: 100%;
            height: 16%;
            top: -6%;
            left: 0;
            background-color: #ffffff;
            border-radius: 0 0 100% 100%;
        }
        .lower-div {
            position: absolute;
            z-index: 1;
            width: 100%;
            height: 16%;
            bottom: -6%;
            left: 0;
            background-color: #ffffff;
            border-radius: 100% 100% 0 0;
        }
        .slides-div {
            width: 100%;
            height: 100%;
            overflow: hidden;
            // background-color: green;
            display: flex;

            .img-div {
                min-width: 100%;
                height: 100%;
                transition: 2s;
                // background-color: blue;
                img {
                    width: 100%;
                    height: 100%;
                    object-fit: contain;
                    // filter: blur(3px);
                }
            }
        }
    }
    .dot-container {
        position: absolute;
        bottom: 0%;
        width: 40%;
        height: 10%;
        // background-color: aqua;
        display: flex;
        justify-content: center;
        align-items: center;

        .dot-button-div {
            min-width: 3vw;
            height: 3vw;
            // margin: 0 20px;
            box-sizing: content-box;
            padding: 5px 20px;
            // background-color: bisque;
            display: flex;
            justify-content: center;
            align-items: center;
        }
    }
}

