.our-ask-comp-main-container {
    width: 100%;
    height: 95vh;
    // height: 850px;
    // border: 1px solid black;
    overflow: hidden;
    position: relative;

    .hidden-hover-div {
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        // background-color: rgba(0,0,0,0.5);
        z-index: 2;
    }

    .hidden-div {
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        background-color: rgba(0, 0, 0, 0.8);
        // z-index: 2;
        display: flex;
        justify-content: center;
        align-items: center;
        opacity: 0;

        img {
            width: 80%;
            height: 80%;
            object-fit: contain;
            // filter: blur(3px);
        }
    }

    .heading-div {
        width: 15%;
        height: 10%;
        position: absolute;
        top: 5%;
        left: 2%;
        // border: 1px solid black;
        display: flex;

        .shape1-div {
            width: 20%;
            height: 100%;
            background-color: #ff4d4d;
        }

        h1 {
            position: absolute;
            bottom: 0;
            left: 25%;
            font-size: 3vw;
            font-weight: 500;
            color: #333333;
            line-height:3vw;
        }
    }

    .funding-request-div {
        position: absolute;
        top: 27%;
        // z-index: 2;
        // border: 1px solid black;
        width: 22%;
        height: 54%;
        border-radius: 0 3vw 3vw 0;
        background-color: #ff4d4d;

        .hr-line-div {
            position: absolute;
            left: 5%;
            top: 15%;
            z-index: 2;
            width: 12%;
            height: 85%;
            border-left: 0.1vw solid #ffffff;
            border-top: 0.1vw solid #ffffff;
            border-radius: 2vw 0 0 0;
        }

        h1 {
            font-size: 2vw;
            font-weight: 500;
            text-shadow: 0.1vw 0.2vw #333333;
            color: #ffffff;
            position: absolute;
            left: 18%;
            top: 10%;
        }

        h2 {
            font-size: 1.5vw;
            font-weight: 300;
            text-shadow: 1px 2px #333333;
            color: #ffffff;
            border-bottom: 1px solid #ffffff;
            display: inline-block;
            padding-bottom: 2%;
            position: absolute;
            left: 18%;
            top: 36%;

            span {
                font-size: 2vw;
                font-weight: 400;
            }
        }

        .amount-div {
            width: 40%;
            height: 20%;
            background-color: #ffffff;
            position: absolute;
            right: -5%;
            top: 35%;
            box-shadow: 0px 0.2vw 0.3vw gray;
            border-radius: 10px 0 0 10px;
            display: flex;
            justify-content: center;
            align-items: center;

            .para1,
            .para2,
            .para3 {
                // text-shadow: 0.1vw 0.1vw gray;
                color: #ff4d4d;
            }

            .para1 {
                font-size: 2.5vw;
                font-weight: 600;
                color: #333333;
                margin: 0 2% 10% 0;
            }

            .para2 {
                font-size: 3.5vw;
                font-weight: 500;
                color: #ff4d4d;
            }

            .para3 {
                font-size: 2.5vw;
                margin-top: 10%;
                color: #333333;
            }
        }

        button {
            position: absolute;
            bottom: 10%;
            font-size: 1.5vw;
            left: 28%;
            padding: 1% 5%;
            border-radius: 50px;
            color: #fff;
            border: none;
            background-color: #0A0A0A;
            box-shadow: 0.1vw 0.1vw 5px #111;
            cursor: pointer;
            // z-index: 3;
        }
    }

    .use-of-funds-div {
        position: absolute;
        top: 5%;
        right: 0;
        z-index: 0;
        // border: 1px solid black;
        width: 82%;
        height: 90%;
        border-radius: 20px;
        background-color: #f6f6f6;
        display: flex;
        justify-content: center;
        align-items: center;

        .property-name-style {
            position: absolute;
            width: 15%;
            height: 5%;
            // border: 1px solid black;
            display: flex;
            align-items: flex-end;
            gap: 5%;

            .shape {
                width: 10%;
                height: 100%;
            }

            p {
                font-size: 1.8vw;
                font-weight: 500;
                color: #333333;
            }
        }

        .propertyOne-name-div {
            @extend .property-name-style;
            top: 22%;
            left: 10%;

            .shape {
                background-color: #f46d1c;
            }
        }

        // .propertyTwo-name-div {
        //     @extend .property-name-style;
        //     top: 42%;
        //     left: 20%;

        //     .shape {
        //         background-color: #0fedd2;
        //     }
        // }

        .propertyThree-name-div {
            @extend .property-name-style;
            top: 70.5%;
            left: 11%;

            .shape {
                background-color: #ffd23b;
            }
        }

        .propertyFour-name-div {
            @extend .property-name-style;
            top: 15%;
            right: 10%;

            .shape {
                background-color: #ff62e8;
            }
        }

        .propertyFive-name-div {
            @extend .property-name-style;
            top: 72%;
            right: 10%;

            .shape {
                background-color: #2f72c3;
            }
        }

        .propertyOne-hr {
            position: absolute;
            top: 24.5%;
            left: 22%;
            width: 15vw;
            height: 4.8vw;
            border-top: 1px solid #43fde7;
            border-right: 1px solid #43fde7;
            transform: skew(45deg);
        }

        // .propertyTwo-hr {
        //     position: absolute;
        //     top: 45%;
        //     left: 10%;
        //     width: 5vw;
        //     height: 3vw;
        //     border-top: 1px solid #43fde7;
        //     border-right: 1px solid #43fde7;
        //     transform: skew(65deg);
        // }

        .propertyThree-hr {
            position: absolute;
            top: 65%;
            left: 26%;
            width: 6vw;
            height: 5vw;
            border-bottom: 1px solid #43fde7;
            border-right: 1px solid #43fde7;
            transform: skew(-55deg);
        }

        .propertyFour-hr {
            position: absolute;
            top: 18%;
            right: 26%;
            width: 22vw;
            height: 5.5vw;
            border-top: 1px solid #43fde7;
            border-left: 1px solid #43fde7;
            transform: skew(-25deg);
        }

        .propertyFive-hr {
            position: absolute;
            top: 66%;
            right: 28%;
            width: 10vw;
            height: 5vw;
            border-bottom: 1px solid #43fde7;
            border-left: 1px solid #43fde7;
            transform: skew(35deg);
        }

        .hr-line-2-div {
            position: absolute;
            left: 2%;
            top: 5%;
            width: 40%;
            height: 30%;
            border-top: 1px solid #707070;
            border-left: 1px solid #707070;
            border-top-left-radius: 20px;
        }

        .heading-two-div {
            width: auto;
            height: auto;
            position: absolute;
            left: 40%;
            top: 1%;
            background-color: #f6f6f6;

            h1 {
                font-size: 2.5vw;
                font-weight: 500;
                text-shadow: 1px 1px gray;
                color: #333333;

                span {
                    color: #ff4d4d;
                }
            }
        }

        .circle-div {
            width: 26vw;
            height: 26vw;
            border-radius: 50%;
            // background-color: #ff4d4d;
            display: flex;
            justify-content: center;
            align-items: center;
            overflow: hidden;
            position: relative;

            .img-one {
                position: absolute;
                top: 0;
                left: 0;
                width: 100%;
                height: 100%;
                object-fit: cover;
                z-index: 0;
            }

            .small-circle-div {
                position: absolute;
                width: 35%;
                height: 35%;
                border-radius: 50%;
                background-color: #ffffff;
                display: flex;
                justify-content: center;
                align-items: center;
                z-index: 1;

                .img-two {
                    width: 50%;
                    height: 50%;
                    object-fit: contain;
                }
            }

        }

        .bottom-para {
            position: absolute;
            bottom: 2%;
            right: 10%;
            width: 22%;
            height: 8%;
            // background-color: #222;
            border-radius: 0 0 20px 0;
            border: none;
            border-right: 0.1vw solid #707070;
            border-bottom: 0.1vw solid #707070;
            display: flex;
            justify-content: center;
            align-items: flex-end;

            p{
                font-size: 1.2vw;
                font-weight: 400;
                margin-bottom: 5%;

                span{
                    font-weight: 600;
                    color: #FF4F4F;
                }
            }
        }
    }
}

