.join-us-comp-main-container {
  width: 100%;
  // height: 850px;
  height: 95vh;
  position: relative;
  // border: 1px solid black;
  display: flex;

  // .hidden-frame-div {
  //   position: absolute;
  //   width: 100%;
  //   height: 100%;
  //   top: 0;
  //   left: 0;
  //   background-color: rgba(0, 0, 0, 0.8);
  //   z-index: 2;
  //   display: flex;
  //   justify-content: center;
  //   align-items: center;

  //   .main-frame {
  //     width: 80%;
  //     height: 90%;
  //     position: relative;
  //     display: flex;
  //     justify-content: center;
  //     align-items: center;

  //     .cross-icon {
  //       position: absolute;
  //       top: 2%;
  //       right: 13%;
  //       // background-color: #333333;
  //       width: 5vw;
  //       height: 5vw;
  //       display: flex;
  //       justify-content: center;
  //       align-items: center;

  //       #crossIcon {
  //         position: absolute;
  //         color: #333;
  //         opacity: 0.9;
  //         font-size: 3vw;
  //         cursor: pointer;

  //         &:hover {
  //           color: #333333;
  //           opacity: 1;
  //         }
  //       }
  //     }

  //     #iframe-calendly {
  //       width: 80%;
  //       height: 100%;
  //       border: none;
  //       border-radius: 1vw;
  //     }
  //   }
  // }

  .left-part {
    width: 60%;
    height: 100%;
    // border: 1px solid green;
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 1%;

    .top-part {
      width: 100%;
      height: 30%;
      padding: 0 0 0 5%;
      // background-color: aqua;
      display: flex;
      justify-content: space-between;
      align-items: center;

      .join-us-heading {
        width: 100%;
        display: flex;
        align-items: center;
        gap: 5%;

        .logo-img {
          width: 8vw;
          height: 8vw;
        }

        h1 {
          font-size: 2.5vw;
          font-weight: 500;
          color: #333333;
          padding-bottom: 2%;
          opacity: 0.9;
          border-bottom: 1px solid #333333;

          span {
            font-weight: 600;
            color: #ff4d4d;
          }
        }
      }

      .icons-div {
        width: 50%;
        height: 100%;
        display: flex;
        // background-color: bisque;
        display: flex;
        justify-content: space-evenly;
        align-items: center;

        a {
          img {
            width: 4vw;
            height: 4vw;
            cursor: pointer;
            opacity: 0.8;

            &:hover {
              transform: scale(1.05);
              opacity: 1;
            }
          }
        }
      }
    }

    .middle-part {
      width: 100%;
      height: 60%;
      display: flex;
      flex-direction: column;
      justify-content: center;
      // align-items: center;
      gap: 15%;
      // background-color: orangered;

      @keyframes tapIcon-KeyFrame {
        0% {
          padding-bottom: 0%;
        }

        60% {
          padding-bottom: 20%;
        }

        100% {
          padding-bottom: 0%;
        }
      }

      .frame-calendly-btn {
        width: 65%;
        height: 22%;
        margin-left: 10%;
        // border: 1px solid black;
        position: relative;

        img {
          width: 100%;
          height: 100%;
          object-fit: contain;
          cursor: pointer;
        }

        .tap-icon-div {
          position: absolute;
          bottom: 10%;
          right: 1.5%;

          .tap-icon {
            font-size: 3.5vw;
            color: #ffffff;
          }
        }
      }
    }

    .bottom-part {
      width: 90%;
      height: 10%;
      // background-color: aqua;
      display: flex;
      justify-content: space-between;
      align-items: center;
      border-bottom: 1px solid #878787;
      margin-left: 10%;
      margin-bottom: 3.8%;

      .our-handles-div {
        width: 50%;
        display: flex;
        align-items: center;
        display: flex;
        align-items: center;

        h1 {
          font-size: 2vw;
          font-weight: 500;
          color: #333333;
          opacity: 0.9;

          span {
            color: #ff4d4d;
          }
        }
      }

      .icons-div {
        width: 50%;
        height: 100%;
        display: flex;
        // background-color: bisque;
        display: flex;
        justify-content: space-evenly;
        align-items: center;

        a {
          img {
            width: 3vw;
            height: 3vw;
            cursor: pointer;
            opacity: 0.8;

            &:hover {
              transform: scale(1.05);
              opacity: 1;
            }
          }
        }
      }
    }
  }

  .right-part {
    width: 40%;
    height: 100%;
    // border: 1px solid green;
    position: relative;

    img {
      position: absolute;
      right: 0;
      bottom: 0;
      width: 90%;
      // height: 80%;
      object-fit: cover;
    }
  }
}




