.marketing-comp-main-container {
    width: 100%;
    // height: 850px;
    height: 95vh;
    // border: 1px solid black;
    background-color: #fafafa;
    border-radius: 20px;
    display: flex;
    position: relative;
    overflow: hidden;

    .hidden-div {
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        // background-color: #333333;
    }

    .left-part-div {
        width: 75%;
        height: 100%;
        // border: 1px solid black;
        display: flex;
        flex-direction: column;
        justify-content: center;

        .top-part-div {
            width: 100%;
            height: 25%;
            position: relative;
            // border: 1px solid black;

            .heading-div {
                position: absolute;
                top: 0;
                left: 2%;
                width: 20%;
                height: 45%;
                // border: 1px solid black;
                display: flex;
                align-items: flex-end;

                .shape1-div {
                    width: 20%;
                    height: 100%;
                    background-color: #ff4d4d;
                }
                button {
                    border: none;
                    background: none;
                    margin: 0 -10% -4% 4%;

                    span {
                        font-size: 2.5vw;
                        color: #333333;
                        text-shadow: 1px 1px gray;
                        cursor: pointer;
                    }
                }
            } //----------------------------------------

            .social-media-icons-div {
                position: absolute;
                top: 60%;
                left: 8%;
                width: 11.5%;
                height: 25%;
                // border: 1px solid black;
                display: flex;
                justify-content: space-around;
                align-items: center;

                img {
                    width: 2vw;
                    height: 2vw;
                    transition: 0.2s;
                    opacity: 0.8;

                    &:hover {
                        cursor: pointer;
                        transform: scale(1.05);
                    }
                }
            } //----------------------------------------

            @keyframes headingKeyframe2 {
                0% {
                    top: -200%;
                    opacity: 0;
                }
                100% {
                    top: 5%;
                }
            }

            .heading2-div {
                width: 70%;
                height: 80%;
                // border: 1px solid black;
                position: absolute;
                left: 26%;
                top: 0;
                display: flex;
                justify-content: center;
                align-items: center;
                overflow: hidden;

                .main-heading-div {
                    width: 60%;
                    height: 80%;
                    text-align: center;
                    display: flex;
                    flex-direction: column;

                    .para {
                        width: 100%;
                        height: 50%;
                        display: flex;
                        justify-content: center;
                        align-items: flex-end;
                        // background-color: red;

                        p {
                            font-size: 1.4vw;
                            color: #333333;
                            font-weight: 500;
                            text-shadow: 1px 1px gray;
                        }
                    }
                    .headings {
                        width: 100%;
                        height: 45%;
                        display: flex;
                        justify-content: center;
                        align-items: center;
                        // background-color: #555;
                        position: relative;

                        h1 {
                            position: absolute;
                            top: 0%;
                            font-size: 2vw;
                            color: #333333;
                            font-weight: 600;
                            letter-spacing: 0.5vw;
                            text-shadow: 1px 1px gray;
                            opacity: 0;

                            span {
                                color: #ff4d4d;
                            }
                        }
                    }
                }
            } //----------------------------------------
            //------------------------------------------------
        }
        .bottom-part-div {
            width: 100%;
            height: 75%;
            position: relative;
            // border: 1px solid black;
        }
    }
    .right-part-div {
        width: 25%;
        height: 100%;
        // border: 1px solid black;
        display: flex;
        justify-content: center;
        align-items: center;
    }
}

