.marketing-nev-bar-main-container {
    width: 90%;
    height: 96%;
    background-color: #ff4d4d;
    display: flex;
    flex-direction: column;
    justify-content: space-evenly;
    position: relative;

    .nev-item-style {
        width: 100%;
        height: 10%;
        // border: 1px solid black;
        display: flex;
        align-items: center;
        position: relative;
    }

    .circle-style {
        position: absolute;
        width: 3.6vw;
        height: 3.6vw;
        border-radius: 50%;
        left: 18%;
        // background-color: #333333;
        background-color: #ffffff;
        display: flex;
        justify-content: center;
        align-items: center;
        transition: 0.5s;

        p {
            font-size: 2vw;
            font-weight: 600;
            text-shadow: 1px 1px gray;
            color: #333333;
        }
    }

    @keyframes tapIcon-KeyFrame {
        0% {
            padding-bottom: 0%;
        }
        60% {
            padding-bottom: 20%;
        }
        100% {
            padding-bottom: 0%;
        }
    }

    .tap-icon-style {
        position: absolute;
        left: 5%;
        // border: 1px solid black;

        .tap-icon {
            font-size: 3vw;
            color: #fff;
        }
    }

    @keyframes btn-keyframe {
        0% {
            opacity: 0;
            background-color: #ff4d4d;
        }
        100% {
            opacity: 1;
            background-color: #333333;
        }
    }

    .btn-style {
        position: absolute;
        right: 0;
        width: 15vw;
        height: 3.2vw;
        background: none;
        border: none;
        // background-color: pink;
        transition: ease-in-out;
    }

    .nev-item-one-div {
        @extend .nev-item-style;
        .circle-div {
            @extend .circle-style;
        }
        button {
            @extend .btn-style;
        }
        .tap-icon-div {
            @extend .tap-icon-style;
        }
    }

    .nev-item-two-div {
        @extend .nev-item-style;

        .circle-div {
            @extend .circle-style;
        }
        button {
            @extend .btn-style;
        }
        .tap-icon-div {
            @extend .tap-icon-style;
        }
    }

    .nev-item-three-div {
        @extend .nev-item-style;

        .circle-div {
            @extend .circle-style;
        }
        button {
            @extend .btn-style;
        }
        .tap-icon-div {
            @extend .tap-icon-style;
        }
    }

    .nev-item-four-div {
        @extend .nev-item-style;

        .circle-div {
            @extend .circle-style;
        }
        button {
            @extend .btn-style;
        }
        .tap-icon-div {
            @extend .tap-icon-style;
        }
    }

    .nev-item-five-div {
        @extend .nev-item-style;

        .circle-div {
            @extend .circle-style;
        }
        button {
            @extend .btn-style;
        }
        .tap-icon-div {
            @extend .tap-icon-style;
        }
    }
}

