.progress-circle-main-container {
    width: 100%;
    height: 36vw;
    // background-color: black;
    // border: 1px solid black;
    position: absolute;
    top: 16%;
    z-index: 0;
    overflow: hidden;

    .circle-img-style {
        width: 100%;
        height: 100%;
        position: absolute;
        bottom: 0;
        // right: -25%;
        // background-color: pink;
        img {
            position: absolute;
            bottom: -10%;
            right: -22%;
            width: 60%;
            height: 60%;
            object-fit: contain;
        }
        .heading-style {
            position: absolute;
            right: 6%;
            font-size: 1.8vw;
            font-weight: 500;
            text-shadow: 0px 2px 2px #333333;
        }
      
    }

    .circle-img-div1 {
        @extend .circle-img-style;
        z-index: 0;
        h1 {
            @extend .heading-style;
            bottom: 26%;
            color: #ffffff;
        }
      
    }
    .circle-img-div2 {
        @extend .circle-img-style;
        z-index: 1;
        h1 {
            @extend .heading-style;
            bottom: 28%;
            color: #333333;
        }
      
    }
    .circle-img-div3 {
        @extend .circle-img-style;
        z-index: 2;
        h1 {
            @extend .heading-style;
            bottom: 28%;
            color: #ffffff;
        }
      
    }
}

