.meet-our-team-comp-main-container {
    width: 100%;
    // height: 850px;
    height: 95vh;
    // border: 1px solid black;
    position: relative;
    overflow: hidden;
    border-radius: 1vw;

    .linked-in-div-style {
        position: absolute;
        // z-index: 1;
        width: 3vw;
        height: 3vw;
        border-radius: 50%;
        background-color: #ffffff;

        a {
            img {
                position: absolute;
                top: 0;
                left: 0;
                width: 100%;
                height: 100%;
                object-fit: contain;
                cursor: pointer;

                &:hover {
                    transform: scale(1.05);
                }
            }
        }
    }

    .linked-in-img-div1 {
        @extend .linked-in-div-style;
        top: 26%;
        left: 28.5%;
    }

    .linked-in-img-div2 {
        @extend .linked-in-div-style;
        top: 47%;
        left: 40.5%;
    }
    .linked-in-img-div3 {
        @extend .linked-in-div-style;
        top: 70%;
        left: 28.5%;
    }

    .bg-img {
        position: absolute;
        top: 0;
        left: -5%;
        width: 100%;
        height: 100%;
        object-fit: contain;
        z-index: 0;
    }

    .heading-div {
        position: absolute;
        right: 0;
        top: 0%;
        width: 25%;
        height: 12%;
        // border: 1px solid black;
        display: flex;
        align-items: center;

        // h1 {
        //     font-size: 2rem;
        //     font-weight: 400;
        //     opacity: 0.9;

        //     span {
        //         font-weight: 600;
        //         color: #ff4d4d;
        //         border-bottom: 2px solid #ff4d4d;
        //     }
        // }

        .shape-div {
            position: absolute;
            top: 0;
            left: 0;
            width: 100%;
            height: 10%;
            background-color: #ff4d4d;
        }
        .hr-line-div {
            position: absolute;
            bottom: 0;
            left: 0;
            width: 100%;
            height: 4%;
            background-color: #333333;
        }
    }

    .details-hover-div {
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        background-color: rgba(0, 0, 0, 0.8);
        display: flex;
        justify-content: center;
        align-items: center;
        z-index: 1;

        .team-img {
            margin-top: 5%;
            width: 70%;
            height: 85%;
            object-fit: contain;
        }
    }
    @keyframes tapIcon-keyframe {
        0% {
            padding-bottom: 0%;
        }
        60% {
            padding-bottom: 20%;
        }
        100% {
            padding-bottom: 0%;
        }
    }

    .tech-team-div {
        width: 25%;
        height: 14%;
        border-top-left-radius: 4vw;
        background-color: #464646;
        position: absolute;
        right: 0;
        bottom: 15%;
        display: flex;
        justify-content: center;
        align-items: center;
        box-shadow: -0.5vw 0.5vw 4px #333333;
        cursor: pointer;
        z-index: 2;
        transition: 0.2s;

        @keyframes teamText-keyframe {
            0% {
                padding-bottom: 0%;
            }
            60% {
                padding-bottom: 8%;
            }
            100% {
                padding-bottom: 0%;
            }
        }

        &:hover {
            background-color: #ff4d4d;
            // box-shadow: none;
            .bullet-point,
            h1,
            h2 {
                transform: scale(1.1);
                transition: 0.2s;
            }
            .name-heading {
                gap: 8%;
                transition: 0.2s;
            }
        }

        .name-heading {
            width: 100%;
            display: flex;
            // justify-content: center;
            padding-left: 15%;
            align-items: center;
            gap: 5%;

            .bullet-point {
                min-width: 1vw;
                min-height: 1vw;
                background-color: aliceblue;
                border-radius: 50%;
                text-shadow: 1px 1px gray;
            }
            h2 {
                font-size: 1.6vw;
                color: #ffffff;
                font-weight: 500;
                text-shadow: 1px 1px gray;
                //--------disabling text selection-------
                -moz-user-select: none; /* firefox */
                -webkit-user-select: none; /* Safari */
                -ms-user-select: none; /* IE*/
                user-select: none; /* Standard syntax */
            }
            h1 {
                font-size: 3.5vw;
                color: #ffffff;
                font-weight: 500;
                text-shadow: 1px 1px gray;
                //--------disabling text selection-------
                -moz-user-select: none; /* firefox */
                -webkit-user-select: none; /* Safari */
                -ms-user-select: none; /* IE*/
                user-select: none; /* Standard syntax */
            }
            .tap-icon-div {
                position: absolute;
                bottom: 1%;
                left: 2.5%;
                .tap-icon {
                    font-size: 3.5vw;
                    color: #ffffff;
                }
            }
        }
    }
    .non-tech-team-div {
        width: 25%;
        height: 14%;
        background-color: #464646;
        position: absolute;
        right: 0;
        bottom: 0;
        display: flex;
        justify-content: center;
        align-items: center;
        box-shadow: -0.5vw 0.5vw 4px #333333;
        cursor: pointer;
        z-index: 2;
        transition: 0.2s;

        &:hover {
            background-color: #ff4d4d;
            // box-shadow: none;
            .bullet-point,
            h1,
            h2 {
                transform: scale(1.1);
                transition: 0.2s;
            }
            .name-heading {
                gap: 8%;
                transition: 0.2s;
            }
        }

        .name-heading {
            width: 100%;
            display: flex;
            // justify-content: center;
            padding-left: 15%;
            align-items: center;
            gap: 5%;

            .bullet-point {
                min-width: 1vw;
                min-height: 1vw;
                background-color: aliceblue;
                border-radius: 50%;
                text-shadow: 1px 1px gray;
            }
            h2 {
                font-size: 1.6vw;
                color: #ffffff;
                font-weight: 500;
                text-shadow: 1px 1px gray;
                //--------disabling text selection-------
                -moz-user-select: none; /* firefox */
                -webkit-user-select: none; /* Safari */
                -ms-user-select: none; /* IE*/
                user-select: none; /* Standard syntax */
            }
            h1 {
                font-size: 3.5vw;
                color: #ffffff;
                font-weight: 500;
                text-shadow: 1px 1px gray;
                //--------disabling text selection-------
                -moz-user-select: none; /* firefox */
                -webkit-user-select: none; /* Safari */
                -ms-user-select: none; /* IE*/
                user-select: none; /* Standard syntax */
            }
            .tap-icon-div {
                position: absolute;
                bottom: 1%;
                left: 2.5%;
                .tap-icon {
                    font-size: 3.5vw;
                    color: #ffffff;
                }
            }
        }
    }
}

