.marketing-sub-comp-main-container {
    width: 100%;
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: flex-end;
    // background-color: yellow;
    overflow: hidden;
    position: relative;

    .heading-div {
        position: absolute;
        width: 60%;
        height: 20%;
        // border: 1px solid black;
        top: 10%;
        left: 25%;
        display: flex;
        justify-content: center;
        align-items: center;

        @keyframes headingKeyframe {
            0% {
                top: 10%;
                left: 25%;
                opacity: 1;
            }
            100% {
                top: -10%;
                left: 30%;
                opacity: 0;
            }
        }

        .heading-style {
            font-size: 2vw;
            color: #333333;
            font-weight: 600;
            text-shadow: 1px 1px gray;
            width: 30%;
            height: 100%;
            display: flex;
            justify-content: center;
            align-items: center;
            transition: 0.5s;
        }

        .headingOne {
            @extend .heading-style;
        }
        .headingTwo {
            @extend .heading-style;
        }
        .headingThree {
            @extend .heading-style;
        }
    }

    .phones-div {
        width: 80%;
        height: 40%;
        margin-left: 10%;
        // border: 1px solid black;
        display: flex;
        justify-content: center;
        align-items: center;

        .phone-image-style {
            width: 34%;
            height: 230%;
            // border: 1px solid black;

            img {
                width: 100%;
                height: 100%;
                object-fit: contain;
                margin-top: 40%;
            }
        }

        .phone1-img-div {
            @extend .phone-image-style;
        }
        .phone2-img-div {
            @extend .phone-image-style;
        }
        .phone3-img-div {
            @extend .phone-image-style;
        }
    }
}

