.logo-vector-mobile-comp-main-container {
    position: absolute;
    width: 45vw;
    height: 95vw;
    // background-color: #fff;
    z-index: 1;
    display: flex;
    justify-content: center;
    overflow: hidden;
    transform: translate(-250%, -37.5%) rotate(0deg);
    // transform: translateY(37.5%);
    transition: ease-in-out 1.5s;

    img {
        transition: 0.5s;
    }

    .part-one {
        object-fit: contain;
    }

    .part-two {
        object-fit: contain;
    }

    .part-three {
        object-fit: contain;
    }

    .part-four {
        object-fit: contain;
    }

    .bottom-part {
        position: absolute;
        top: 0%;
        left: 0%;
        width: 104%;
        height: 104%;
        overflow: hidden;
        transition: ease-in-out 1.5s;
        transform: scale(-1);

        img {
            position: absolute;
            top: 1.2%;
            left: 5.5%;
            width: 45.8vw;
            min-height: 100%;
            object-fit: contain;
            z-index: 0;
            transition: ease-in-out 1.5s;
            transform: scale(-1);

        }

    }

}