.business-model-comp-main-container {
    width: 100%;
    height: 95vh;
    display: flex;
    justify-content: center;
    align-items: center;
    overflow: hidden;
    position: relative;

    .heading {
        position: absolute;
        top: 5%;
        font-size: 3vw;
        line-height: 3.2vw;
        font-weight: 400;
        text-align: center;
        text-shadow: 1px 1px #333;

        span {
            color: #ff4d4d;
            font-weight: 600;
        }
    }

    .pricing-details-div-style {
        position: absolute;
        width: 100%;
        height: 100%;
        top: 0;
        left: 0;
        opacity: 0;
        // background-color: #333333;
        background-color: rgba(0, 0, 0, 0.8);
        padding: 1% 2%;
        box-sizing: border-box;
        transition: 0.5s;
        display: flex;

        .pricing-img-div {
            position: relative;
            width: 50%;
            height: 48%;
            display: flex;
            // border: 1px solid black;
            justify-content: center;
            align-items: center;

            .pricing-img {
                // position: absolute;
                // top: 0;
                width: 100%;
                height: 100%;
                object-fit: contain;
                // filter: blur(6px);


            }

            .cross-icon-div {
                position: absolute;
                cursor: pointer;
                background-color: #ff4d4d;
                border-radius: 50%;
                z-index: 1;
                width: 3vw;
                height: 3vw;
                display: flex;
                justify-content: center;
                align-items: center;

                .cross-icon {
                    color: #ffffff;
                    font-size: 2vw;
                }
            }
        }
    }

    .pricing-details-div-one {
        @extend .pricing-details-div-style;
        justify-content: left;

        .cross-icon-div {
            left: 0%;
            top: 0%;
        }
    }

    .pricing-details-div-five {
        @extend .pricing-details-div-style;
        justify-content: left;
        align-items: center;

        .cross-icon-div {
            left: 0%;
            top: 0%;
        }
    }

    .pricing-details-div-two {
        @extend .pricing-details-div-style;
        justify-content: right;

        .cross-icon-div {
            right: 0%;
            top: 0%;
        }
    }

    .pricing-details-div-three {
        @extend .pricing-details-div-style;
        justify-content: left;
        align-items: flex-end;

        .cross-icon-div {
            left: 0%;
            top: 0%;
        }
    }

    .pricing-details-div-six {
        @extend .pricing-details-div-style;
        justify-content: right;
        align-items: center;

        .cross-icon-div {
            right: 0%;
            top: 0%;
        }
    }

    .pricing-details-div-four {
        @extend .pricing-details-div-style;
        justify-content: right;
        align-items: flex-end;

        .cross-icon-div {
            right: 0%;
            top: 0%;
        }
    }

    .shape-style {
        position: absolute;
        width: 4%;
        height: 8%;
        background-color: #ff4d4d;
    }

    .shape-one {
        @extend .shape-style;
        left: 0;
        top: 0;
        box-shadow: 0.25vw 0.25vw 2px gray;
    }

    .shape-two {
        @extend .shape-style;
        left: 0;
        bottom: 0;
        box-shadow: 0.25vw -0.25vw 2px gray;
    }

    .shape-three {
        @extend .shape-style;
        right: 0;
        top: 0;
        box-shadow: -0.25vw 0.25vw 2px gray;
    }

    .shape-four {
        @extend .shape-style;
        right: 0;
        bottom: 0;
        box-shadow: -0.25vw -0.25vw 2px gray;
    }

    .circle-div {
        width: 25vw;
        height: 25vw;
        border-radius: 50%;
        // background-color: #ff4d4d;
        display: flex;
        justify-content: center;
        align-items: center;
        overflow: hidden;
        position: relative;
        transition: 0.2s;

        .small-circle-div {
            position: absolute;
            width: 35%;
            height: 35%;
            border-radius: 50%;
            background-color: #ffffff;
            display: flex;
            justify-content: center;
            align-items: center;
            z-index: 2;

            .img-two {
                width: 65%;
                height: 65%;
                object-fit: contain;
                // filter: blur(4px);
            }
        }

        .image-style {
            position: absolute;
            width: 50%;
            height: 50%;
            // z-index: 0;
            // cursor: pointer;
            overflow: hidden;

            img {
                position: absolute;
                object-fit: contain;
                // filter: blur(3px)
            }
        }

        .img-div-one {
            @extend .image-style;
            left: -0.5%;
            top: -0.5%;
            // background-color: green;

            img {
                // top: -10%;
                // left: -220%;
                top: -85%;
                left: -160%;
                width: 300%;
                height: 300%;
                object-fit: contain;
            }
        }

        .img-div-two {
            @extend .image-style;
            right: -0.5%;
            top: -0.5%;
            background-color: #ff4f4f;

            img {
                // top: 45%;
                left: 5%;
                top: 30%;
                width: 120%;
                height: 100%;
                object-fit: contain;
            }
        }

        .img-div-three {
            @extend .image-style;
            left: -0.5%;
            bottom: -0.5%;
            // background-color: brown;

            img {
                // top: -45%;
                // left: -30%;
                top: -55%;
                left: -10%;
                width: 160%;
                height: 200%;
                object-fit: contain;
            }
        }

        .img-div-four {
            @extend .image-style;
            right: -0.5%;
            bottom: -0.5%;
            // background-color: orange;

            img {
                // top: -35%;
                // left: -35%;
                top: -66%;
                left: -45%;
                width: 200%;
                height: 200%;
                object-fit: contain;
            }
        }
    }

    .hr-line-style {
        position: absolute;
        width: 12vw;
        height: 8vw;
        z-index: -1;
    }

    .hrline-one {
        @extend .hr-line-style;
        top: 28%;
        left: 32%;
        border-top: 0.2vw solid #ff4d4d;
        border-right: 0.2vw solid #ff4d4d;
        transform: skew(35deg);
    }

    .hrline-five {
        @extend .hr-line-style;
        top: 51%;
        left: 30%;
        border-top: 0.2vw solid #ff4d4d;
        border-right: 0.2vw solid #ff4d4d;
        height: 1px;
    }

    .hrline-two {
        @extend .hr-line-style;
        top: 28%;
        right: 30%;
        border-top: 0.2vw solid #ff4d4d;
        border-left: 0.2vw solid #ff4d4d;
        transform: skew(-35deg);
    }

    .hrline-three {
        @extend .hr-line-style;
        bottom: 22%;
        left: 30%;
        border-bottom: 0.2vw solid #ff4d4d;
        border-right: 0.2vw solid #ff4d4d;
        transform: skew(-35deg);
    }

    .hrline-six {
        @extend .hr-line-style;
        top: 51%;
        right: 30%;
        border-top: 0.2vw solid #ff4d4d;
        border-left: 0.2vw solid #ff4d4d;
        height: 1px;
    }

    .hrline-four {
        @extend .hr-line-style;
        bottom: 22%;
        right: 30%;
        // width: 5vw;
        border-bottom: 0.2vw solid #ff4d4d;
        border-left: 0.2vw solid #ff4d4d;
        transform: skew(35deg);
    }

    .property-para-style {
        position: absolute;
        width: auto;
        z-index: 0;

        p {
            font-size: 1.6vw;
            line-height: 2vw;
            font-weight: 500;

            span {
                font-size: 2vw;
                color: #ff4d4d;
                font-weight: 500;
            }
        }

        .price-chart-btn-style {
            position: absolute;
            background-color: #333;
            width: 15vw;
            height: 3.2vw;
            display: flex;
            justify-content: center;
            align-items: center;
            margin-top: 5%;
            border-radius: 0.4vw;
            cursor: pointer;
            user-select: none;

            &:hover {
                box-shadow: 0.3vw 0.3vw 2px gray;
            }

            .price-chart-btn {
                font-size: 1.5vw;
                color: #ffffff;
                cursor: pointer;
                transition: 0.2s;
                margin: 0;
            }

            .tap-icon-div {
                .tap-icon {
                    margin: 0 0 -30% 20%;
                    font-size: 2.5vw;
                    color: #ffffff;
                }
            }
        }
    }

    @keyframes priceChart-keyframe {
        0% {
            padding-bottom: 0%;
        }

        60% {
            padding-bottom: 8%;
            color: #ff4d4d;
        }

        100% {
            padding-bottom: 0%;
        }
    }

    @keyframes tapIcon-KeyFrame {
        0% {
            padding-bottom: 0%;
        }

        60% {
            padding-bottom: 20%;
        }

        100% {
            padding-bottom: 0%;
        }
    }

    .property-para-one {
        @extend .property-para-style;
        top: 15%;
        left: 12%;

        .price-chart-btn-div {
            @extend .price-chart-btn-style;
            left: 0;
            top: 100%;
            
        }
    }

    .property-para-five {
        @extend .property-para-style;
        top: 45%;
        left: 12%;

        .price-chart-btn-div {
            @extend .price-chart-btn-style;
            left: 0;
            top: 100%;
        }
    }

    .property-para-two {
        @extend .property-para-style;
        top: 15%;
        right: 8%;

        p {
            margin-left: -35%;

        }

        .price-chart-btn-div {
            @extend .price-chart-btn-style;
            right: 0;
            top: 100%;
        }
    }

    .property-para-three {
        @extend .property-para-style;
        bottom: 15%;
        left: 12%;

        .price-chart-btn-div {
            @extend .price-chart-btn-style;
            left: 0;
            top: 100%;
        }
    }

    .property-para-six {
        @extend .property-para-style;
        top: 45%;
        right: 8%;

        p {
            margin-left: -35%;

        }

        .price-chart-btn-div {
            @extend .price-chart-btn-style;
            right: 0;
            top: 100%;
        }
    }

    .property-para-four {
        @extend .property-para-style;
        bottom: 15%;
        right: 8%;

        p {
            margin-left: -35%;

        }

        .price-chart-btn-div {
            @extend .price-chart-btn-style;
            right: 0;
            top: 100%;

        }
    }
}



